














import { mapMutations } from "vuex";
import { Assignment } from "../models/Assignment";

export default {
  name: "InputBox",
  data() {
    return {
      description: undefined,
    };
  },
  methods: {
    ...mapMutations(["addAssignment"]),
    createAssignment(): Assignment {
      return {
        description: this.description,
        isImportant: false,
        deadline: undefined,
        isDone: false,
      };
    },
  },
};
